import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Portfolio from '@/pages/Portfolio'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Abdulsamii |  Home',
      component: Home
    },
    {
      path: '/portfolio',
      name: 'Abdulsamii | Portfolio',
      component: Portfolio
    }
  ]
})



